import React, { useState } from 'react';

function KycChecker() {
  const [email, setEmail] = useState('');
  const [status, setStatus] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  const handleCheckKyc = async (e) => {
    e.preventDefault();
    if (!email) {
      setStatus('Please enter an email address.');
      return;
    }
    setIsLoading(true);
    setStatus('Checking status...');
    try {
      const response = await fetch('/api/getCustomerByEmail', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email }),
      });
      if (!response.ok) throw new Error('Failed to check KYC status.');
      const result = await response.json();
      setStatus(`KYC Status for ${email}: ${result.kycStatus}`);
    } catch (error) {
      console.error('KYC Check Error:', error);
      setStatus('Error checking status. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="kyc-checker">
      <h3>Check Your KYC Status</h3>
      <p>Enter the email you used with Transak to check your verification status.</p>
      <form onSubmit={handleCheckKyc}>
        <input
          type="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          placeholder="Enter your email"
          className="kyc-input"
          disabled={isLoading}
        />
        <button type="submit" className="kyc-button" disabled={isLoading}>
          {isLoading ? 'Checking...' : 'Check Status'}
        </button>
      </form>
      {status && <p className="status-message">{status}</p>}
    </div>
  );
}
export default KycChecker;
