import React, { useState, useEffect } from 'react';
import { ethers } from 'ethers';

function WalletConnector({ onConnect }) {
  const [walletAddress, setWalletAddress] = useState(null);
  const [errorMessage, setErrorMessage] = useState(null);
  const connectWalletHandler = async () => {
    if (window.ethereum) {
      try {
        setErrorMessage(null);
        const provider = new ethers.BrowserProvider(window.ethereum);
        const accounts = await provider.send("eth_requestAccounts", []);
        if (accounts.length > 0) {
          const address = accounts[0];
          setWalletAddress(address);
          onConnect(address);
        }
      } catch (error) {
        setErrorMessage("Failed to connect wallet.");
      }
    } else {
      setErrorMessage("Please install a Web3 wallet like MetaMask.");
    }
  };
  useEffect(() => {
    if (window.ethereum) {
      const handleAccountsChanged = (accounts) => {
        if (accounts.length > 0) {
          setWalletAddress(accounts[0]);
          onConnect(accounts[0]);
        } else {
          setWalletAddress(null);
          onConnect(null);
        }
      };
      window.ethereum.on('accountsChanged', handleAccountsChanged);
      return () => window.ethereum.removeListener('accountsChanged', handleAccountsChanged);
    }
  }, [onConnect]);
  return (
    <div className="wallet-connector">
      {walletAddress ? (
        <div className="wallet-info">
          <p>Connected Wallet:</p>
          <p><strong>{`${walletAddress.substring(0, 6)}...${walletAddress.substring(walletAddress.length - 4)}`}</strong></p>
        </div>
      ) : (
        <button onClick={connectWalletHandler} className="connect-button">Connect Wallet</button>
      )}
      {errorMessage && <p className="error-message">{errorMessage}</p>}
    </div>
  );
}
export default WalletConnector;